@import url('https://fonts.googleapis.com/css2?family=Mate+SC&display=swap');

.home{
    position: absolute;
    
    
}

.title {
    
    position: relative;
    top: -15vh;
    padding-left: 5vw;
    // grid-column: 1 / -1;
    // grid-row: 1;
    color: white;
    font-family: 'Mate SC', serif;
    font-weight: 800;
    width: 100%;
    z-index: 2;

    // start

    > .inner-title {
        display: inline-block;
    }
}

@keyframes text-clip {
    from {
    clip-path: polygon(0% 100%, 100% 100%, 100% 100%, 0% 100%);
    }
    to {
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
    }
}

@keyframes outer-left {
    from {
    transform: translateX(50%);
    }
    to {
    transform: none;
    }
}

@keyframes inner-left {
    from {
    transform: translateX(-50%);
    }
    to {
    transform: none;
    }
}

.hi,.name {
    animation: outer-left 1.5s 2s cubic-bezier(0.5, 0, 0.1, 1) both;
}

.inner-title {
    display: inline-block;
    animation: inner-left 1.5s 2s ease both;
}

.inner-hi {
    display: inline-block;
    animation: inner-left 1.5s 2s ease both, text-clip 1.5s 1s cubic-bezier(0.5, 0, 0.1, 1) both;
}

.inner-name {
    animation: text-clip 1.5s 1s cubic-bezier(0.5, 0, 0.1, 1) both;
}


.title {
    animation: outer-left 1s 2s ease both;
}

.hi {
    font-size: 5.3vw;
    // start
    > .inner-hi {
        display: inline-block;
    }
}

.name {
    font-size: 5vw;
    display: inline-block;
}

.profession{
    font-size: 3vw;
}

.image {
    grid-row: 1;
    grid-column: 2;
    margin-left: -2rem;
    opacity: 0.7;

    animation: image-in 1s cubic-bezier(0.5, 0, 0.1, 1) 2s backwards;

    @keyframes image-in {
        from {
        clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
        }
        to {
        clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
        }
    }

    img {
        display: block;
        width: 100%;
        height: auto;
    }
}

